import React from "react";
import {reduxForm} from "redux-form";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {createField, Input} from "../common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";
import {Navigate} from "react-router-dom";
import style from '../common/FormsControls/FormControls.module.css'

//     ,   
let maxLength = maxLengthCreator(30);
//   
const LoginForm = ({handleSubmit, error}) => {
    return (
        // 'handleSubmit' -            HOC   !?
        <form onSubmit={handleSubmit}>
            {/*createField -     */}
            { createField('Email', Input, 'email', [required, maxLength]) }
            { createField('Password', Input, 'password', [required, maxLength], {type: 'password'})}
            { createField(null, 'input', 'rememberMe', [], {type: "checkbox"},  'remember me')}
            {/*</div>*/}
            {/*/!*    *!/*/}
            { error && <div className={style.formSummaryError}>{error}</div> }
             <div>
                <button>Login</button>
            </div>
        </form>
    )
}
//   'LoginForm', 'reduxForm' - HOC
const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

//     ("LoginForm")
const Login = (props) => {

    const onSubmit = (formData) => {
        //          thunkCreator,      HOC,
        //   callback,       thunkCreator(login)
        //   . 
        props.login(formData.email, formData.password, formData.rememberMe);
    }
    //   
    if (props.isAuth) {
        return <Navigate to={'/profile'}/>
    }
    return (<div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>)
}

const mapStateToProps = (state) => ({
    // isAuth   State
    isAuth: state.authUser.isAuth
})

//        
export default connect(mapStateToProps, {login})(Login);// login -  thunkCreator