import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            };
        default:
            return state;
    }
}
//         STATE
export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    payload: {userId, email, login, isAuth}
})

export const authorizeUserThunkCreator = () => async (dispatch) => {//getUserData//    
    let response = await authAPI.authorizeUser()//    promise,   await "  "

    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));//isAuth     true
    }
}


export const login = (email, password, rememberMe) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe)

    if (response.data.resultCode === 0) {
        //      thunk y 
        dispatch(authorizeUserThunkCreator())
    } else {
        //      resultCode   0,    stopSubmit(" ",
        // -         . response  docAPI
        let messages = response.data.messages.length > 0 ?
            response.data.messages[0] : 'Some error';
        dispatch(stopSubmit('login', {_error: messages}))
    }
}

export const logout = () => async (dispatch) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))//     ""    
    }
}


export default authReducer;